<template>
    <el-dialog
            title="解析日志详情"
            v-model="dialogFormVisible"
            :close-on-click-modal="false"
            width="500px"
            @close="close"
            top="50px"
    >
        <el-form :model="form" label-width="80px">
            <el-form-item label="域名">
                <el-input
                        :model-value="form.domainValue"
                        autocomplete="off"
                        readonly
                ></el-input>
            </el-form-item>
            <el-form-item label="原ip地址">
                <el-input
                        :model-value="form.oldIpAddress"
                        autocomplete="off"
                        readonly
                ></el-input>
            </el-form-item>
            <el-form-item label="新ip地址">
                <el-input
                        :model-value="form.newIpAddress"
                        autocomplete="off"
                        readonly
                ></el-input>
            </el-form-item>
            <el-form-item label="结果状态">
                <el-input
                        value="成功"
                        autocomplete="off"
                        readonly
                        v-if="form.result == 1"
                ></el-input>
                <el-input
                        value="失败"
                        autocomplete="off"
                        readonly
                        v-else
                ></el-input>
            </el-form-item>
            <el-form-item label="结果信息">
                <el-input
                        :model-value="form.message"
                        autocomplete="off"
                        type="textarea"
                        :rows="4"
                        readonly
                ></el-input>
            </el-form-item>
            <el-form-item label="创建时间">
                <el-input
                        :model-value="form.createTime"
                        autocomplete="off"
                        readonly
                ></el-input>
            </el-form-item>
        </el-form>
    </el-dialog>
</template>

<script>
    export default {
        name: "dnsPodLogEdit",
        data() {
            return {
                dialogFormVisible: false,
                form: {},
            }
        },
        methods: {
            showEdit(row) {
                this.form = Object.assign({}, row);
                this.dialogFormVisible = true;
            },
            close() {
                this.form = this.$options.data().form;
                this.dialogFormVisible = false;
            },
        }
    }
</script>

