<template>
    <div class="dns-pod-container">
        <el-row class="query-form">
            <el-col :xs="24" :sm="24" :md="24" :lg="12" :xl="12" class="left-panel">
                <el-button @click="reloadTable"
                ><i class="fa fa-refresh"></i> 刷新</el-button
                >
            </el-col>
            <el-col :xs="24" :sm="24" :md="24" :lg="12" :xl="12" class="right-panel">
                <el-form :inline="true" :model="searchForm" @submit.prevent>
                    <el-form-item style="width: 150px">
                        <el-input
                                v-model.trim="searchForm.domainValue"
                                placeholder="域名"
                                clearable
                                @keyup.enter="search"
                        />
                    </el-form-item>
                    <el-form-item style="width: 100px">
                        <el-select
                                v-model="searchForm.state"
                                placeholder="状态"
                                clearable
                                @change="search"
                        >
                            <el-option label="成功" value="1"></el-option>
                            <el-option label="失败" value="2"></el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item>
                        <el-button type="primary" @click="search">
                            <i class="fa fa-search"></i> 查询
                        </el-button>
                    </el-form-item>
                </el-form>
            </el-col>
        </el-row>
        <el-table
                v-loading="listLoading"
                :data="dnsPodLogs"
                :element-loading-text="elementLoadingText"
        >
            <el-table-column show-overflow-tooltip label="序号">
                <template #default="scope">
                    {{ (page.curPage - 1) * page.pageSize + scope.$index + 1 }}
                </template>
            </el-table-column>
            <el-table-column prop="domainValue" label="域名">
                <template #default="{ row }">
                    <el-tag effect="dark" type="info">{{ row.domainValue }}</el-tag>
                </template>
            </el-table-column>
            <el-table-column prop="oldIpAddress" label="原IP地址"></el-table-column>
            <el-table-column prop="newIpAddress" label="新IP地址"></el-table-column>
            <el-table-column prop="createTime" label="创建时间"></el-table-column>
            <el-table-column prop="result" label="结果状态">
                <template #default="{ row }">
                    <el-tag effect="dark" type="success" v-if="row.result == 1">成功</el-tag>
                    <el-tag effect="dark" type="danger" v-else>失败</el-tag>
                </template>
            </el-table-column>
            <el-table-column label="操作" fixed="right" width="100">
                <template #default="{ row }">
                    <el-button type="text" @click="handleEdit(row)">详情</el-button>
                </template>
            </el-table-column>
        </el-table>
        <el-pagination
                background
                small
                :current-page="page.curPage"
                :page-size="page.pageSize"
                :layout="layout"
                :total="page.total"
                :page-sizes="[8, 10, 20, 30, 40, 50, 100]"
                @size-change="handleChangeSize"
                @current-change="handleChangePage"
        ></el-pagination>
    </div>
    <dns-pod-log-edit ref="detail" />
</template>

<script>
    import { pageDnsPodLog } from "@/api/dnspod/domain"
    import dnsPodLogEdit from "./components/dnsPodLogEdit";
    export default {
        name: "dnsPodLog",
        components: {
          dnsPodLogEdit
        },
        data() {
            return {
                listLoading: true,
                layout: "total, sizes, prev, pager, next, jumper",
                elementLoadingText: "正在加载...",
                page: {
                    curPage: 1,
                    pageSize: 10,
                    total: 0,
                },
                searchForm: {
                    domainValue: "",
                    state: "",
                },
                dnsPodLogs: []
            }
        },
        created() {
          this.pageDnsPodLog()
        },
        methods: {
            pageDnsPodLog() {
                this.listLoading = true;
                this.searchForm.curPage = this.page.curPage;
                this.searchForm.pageSize = this.page.pageSize;
                pageDnsPodLog(this.searchForm).then((res) => {
                    if (res.code == 200) {
                        this.dnsPodLogs = res.data.records
                        this.page.total = res.data.total
                    } else {
                        if (this.page.curPage != 1) {
                            this.search();
                        } else {
                            this.dnsPodLogs = []
                            this.page.total = 0
                        }
                    }
                    this.listLoading = false;
                })
            },
            search() {
                this.page.curPage = 1;
                this.pageDnsPodLog();
            },
            handleEdit(row) {
                this.$refs.detail.showEdit(row);
            },
            handleChangePage(number) {
                this.page.curPage = number;
                this.pageDnsPodLog();
            },
            handleChangeSize(number) {
                this.page.pageSize = number;
                this.pageDnsPodLog();
            },
            reloadTable() {
                this.searchForm = this.$options.data().searchForm;
                this.page.curPage = 1;
                this.pageDnsPodLog();
            },
        }
    }
</script>

<style scoped>

</style>
